.input {
  display: block;
  position: relative;
}
.input__input {
  display: block;
  width: 100%;
  background: #f0f5f8;
  border: 1px solid transparent;
  outline: 0;
  font-size: 16px;
  line-height: 1.2;
  padding: 30px 15px 13px;
  border-radius: 4px;
  color: #628aa1;
  transition: all 0.1s ease-out;
}
.input__input:focus {
